@use '../../theme' as *;

.overview {
  $self: &;
  background: linear-gradient(95deg, $app-grey 50%, $app-black 50%);
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &_bio {
    flex: 75%;
    height: 75%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 8rem;

    #{ $self }_bio_container {
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      max-width: $app-max-width;
      left: 0;
      right: 0;
      margin: 0 auto;

      #{ $self }_desc {
        flex: 50%;
        height: 100%;
        display: flex;
        flex-direction: column;
        padding-left: 15%;
        padding-top: 6rem;
        font-family: 'Raleway';

        #{ $self }_hi {
          font-weight: 600;
          font-size: 1.5rem;
        }

        #{ $self }_name {
          font-weight: 600;
          font-size: 2.5rem;
          padding-top: 1rem;
        }

        #{ $self }_tagline {
          font-weight: 600;
          color: $app-dark-grey;
          font-size: 0.8rem;
        }

        #{ $self }_social {
          padding-top: 2rem;
          display: flex;

          #{ $self }_social_icon {
            background: #C4C4C4;
            box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
            height: 2rem;
            width: 2rem;
            margin-right: 1.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor:pointer; 
            &:hover{
              -webkit-transform: scale(1.05);
              -ms-transform: scale(1.05);
              transform: scale(1.05);
            }

            img {
              height: 60%;
            }
          }
        }
      }

      #{ $self }_image {
        flex: 50%;
        height: 100%;
        display: flex;
        align-items: end;
        justify-content: center;

        img {
          height: 100%;
        }
      }
    }

  }

  &_summary {
    flex: 25%;
    width: 100%;
    background: $app-black;
    display: flex;
    justify-content: center;
    align-items: center;
    #{ $self }_summary_container {
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      max-width: $app-max-width;
      margin: 0 auto;
      color: $app-white;
      padding: 2rem 6rem;
      #{ $self }_summary_label {
        font-size: 1.5rem;
        font-weight: 600;
      }
      #{ $self }_summary_content {
        font-family: 'Raleway';
        font-size: 0.8rem;
        padding-top: 1rem;
      }
    }
  }
}